
import XHeader from 'vux/src/components/x-header/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

import mixins from 'assets/js/mixins.js'
export default {
	name: 'appointment',
	mixins: [mixins.scroller],
	data() {
		return {
			state: {
				0: 'warn', //
				1: 'success', //
				2: 'overdue', //
				3: 'cancel' //
			},
			visitState: [{
				name: '',
				value: ''
			}, {
				name: '',
				value: 0
			}, {
				name: '',
				value: 1
			}, {
				name: '',
				value: 2
			}, {
				name: '',
				value: 3
			}],
			visitValue: [],
		}
	},
	computed: {
		ajaxData() {
			return {
				header: {
					action: 'GetAppointmentList',
				},
				body: {
					UserID: this.$common.getLocalStorage('user').userid,
					State: this.visitValue[0]
				}
			}
		}
	},
	methods: {
		//
		deleteAppointment(opt, index) {
			this.$vux.confirm.show({
				title: '',
				content: '',
				onConfirm: () => {
					this.$common.ajax({
						data: {
							header: {
								action: 'DeleteAppointment',
							},
							body: {
								AppointmentID: opt.ID
							}
						},
						success: (res) => {
							this.list.splice(index, 1);
						}
					})

				}
			})

		}
	},
	mounted() {},
	components: {
		XHeader,
		Spinner,
		PopupPicker,
	}
}



